.slide {
	width: 100vw;
	height: 100vh;
}
.wrapper {
	display: flex;
	flex-direction: row;
	width: 400vw;
	transform: rotate(90deg) translateY(-100vh);
	transform-origin: top left;
}
.one {
	background: #efdefe;
}
.two {
	background: #a3f3d3;
}
.three {
	background: #0bbaa0;
}
.four {
	background: #00dfdf;
}
.outer-wrapper {
	width: 100vh;
	height: 100vw;
	transform: rotate(-90deg) translateX(-100vh);
	transform-origin: top left;
	overflow-y: scroll;
	overflow-x: hidden;
	position: absolute;
	scrollbar-width: none;
	-ms-overflow-style: none;
}
::-webkit-scrollbar {
	display: none;
}
